<template>
  <div class="other-games">
    <h3 class="home__module-header">
      <img class="home__module-icon" :src="$useWebpImg('@/assets/img/home/game_list/icon_game.png')" alt="">
      <span class="home__module-title">其他游戏</span>
    </h3>
    <div class="other-games__content clearfix">
      <ul class="other-games__list other-games__list--left">
        <li v-for="(item,index) in gameListLeft" :key="index"
            @click="handelClick(item)"
            :class="{'max-img':item.maxImg}"
            class="other-games__item default-bg-small">
          <img class="other-games__img" v-lazy="$useWebpImg(item.imgUrl)" src="" alt="">
          <img v-if="!loginStatus&&item.canTry" class="home__icon-try"
               :src="$useWebpImg('@/assets/img/common/icon_try.png')" alt="试玩">
          <div v-if="getGameStatus(item.gameCode)" class="maintain-mask">
            <div class="maintain-mask__inner">
              <img class="maintain-mask__icon" :src="$useWebpImg('@/assets/img/common/icon_time.png')" alt="">
              <p class="maintain-mask__text">维护中</p>
            </div>
          </div>
        </li>
      </ul>
      <ul class="other-games__list other-games__list--right">
        <li v-for="(item,index) in gameListRight" :key="index"
            @click="handelClick(item)"
            :class="{'max-img':item.maxImg}"
            class="other-games__item default-bg-small">
          <img class="other-games__img" v-lazy="$useWebpImg(item.imgUrl)" src="" alt="">
          <img v-if="!loginStatus&&item.canTry" class="home__icon-try"
               :src="$useWebpImg('@/assets/img/common/icon_try.png')" alt="试玩">
          <div v-if="getGameStatus(item.gameCode)" class="maintain-mask">
            <div class="maintain-mask__inner">
              <img class="maintain-mask__icon" :src="$useWebpImg('@/assets/img/common/icon_time.png')" alt="">
              <p class="maintain-mask__text">维护中</p>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import { tryPlayCode, playGame } from '@/utils/gameUtil.js'

export default {
  name: 'game_list',
  data() {
    return {
      gameListLeft: [
        {
          name: '游戏大厅',
          link: '/games',
          maxImg: true,
          canTry: true,
          imgUrl: '@/assets/img/home/game_list/games.png'
        }, {
          name: 'as真人棋牌',
          gameCode: 'A02064',
          canTry: tryPlayCode.A02064,
          imgUrl: '@/assets/img/home/game_list/as_chess.png'
        }, {
          name: 'ag彩票',
          gameCode: 'A02004',
          canTry: tryPlayCode.A02004,
          imgUrl: '@/assets/img/home/game_list/ag_lottery.png'
        }
      ],
      gameListRight: [
        {
          name: '沙巴体育',
          gameCode: 'A02031',
          canTry: tryPlayCode.A02031,
          imgUrl: '@/assets/img/home/game_list/shabab_sports.png'
        }, {
          name: 'ag国际体育',
          gameCode: 'A02026',
          canTry: tryPlayCode.A02026,
          imgUrl: '@/assets/img/home/game_list/ag_sports.png'
        }, {
          name: 'ag国际捕鱼王',
          gameCode: 'A02026',
          gameType: 6,
          maxImg: true,
          canTry: true,
          imgUrl: '@/assets/img/home/game_list/fishing.png'
        }
      ]
    }
  },
  computed: {
    ...mapState(['gameStatus']),
    ...mapGetters(['loginStatus'])
  },
  methods: {
    getGameStatus(code) {
      return this.gameStatus[code] && this.gameStatus[code].flag === 0
    },
    handelClick(item) {
      if (item.gameCode && !this.getGameStatus(item.gameCode)) {
        const params = {
          gameCode: item.gameCode,
          gameType: item.gameType,
          canTryPlay: item.canTry
        }
        return playGame(params)
      }
      this.$router.push(item.link)
    }
  }
}
</script>

