<template>
  <div class="banner default-bg-small">
    <swiper v-if="bannerList.length" :options="swiperOption" class="swiper-container">
      <swiper-slide v-for="(item, index) in bannerList" :key="index"
                    @click.native="handelClick(item, index)"
                    class="default-bg-small">
        <img class="banner__img" :src="item.imgUrl" alt="">
      </swiper-slide>
    </swiper>
    <VideoDialog :showVideoDialog.sync="showVideoDialog" :video="video" :poster="poster"></VideoDialog>
  </div>
</template>

<script>
import VideoDialog from '@/components/dialog/VideoDialog'
import { getUUId } from '@/utils/util'
import { getWmsStatic } from '@/api/modules/wms'

export default {
  name: 'Banner',
  components: { VideoDialog },
  props: {
    field: {
      type: String,
      default: 'index_new'
    }
  },
  data() {
    return {
      bannerList: [],
      swiperOption: {
        loop: true,
        autoplay: {
          delay: 5000,
          disableOnInteraction: false
        },
        on: {
          beforeLoopFix: function () {
            console.log('before')
          },
          loopFix: function () {
            console.log('fix')
          }
        }
      },
      video: null,
      poster: null,
      showVideoDialog: false
    }
  },
  created() {
    this.getBannerList()
  },
  methods: {
    getBannerList() {
      const url = '/_banner/banner.txt'
      const params = {
        params: {
          _: getUUId()
        }
      }
      getWmsStatic(url, params).then(({ data }) => {
        const bannerList = data[this.field].slider_data
        bannerList.forEach(item => {
          item.imgUrl = `${window.dynamicConfig.wmsServer}/${item.imgurl}`
          item.imgUrl += window.isSupportWebp ? '_.webp' : ''
        })
        this.bannerList = bannerList
      })
    },
    openVideoDialog(video, poster) {
      this.poster = poster
      this.video = video
      this.showVideoDialog = true
    },
    handelClick(item, index) {
      if (item.action.detail) {
        switch (item.action.type) {
          case '1':
            this.$router.push(item.action.detail)
            break
          case '2':
            // 进入游戏
            break
          case '3':
            this.openVideoDialog(item.action.detail, item.imgUrl)
            break
        }
      }
      console.log(item, index)
    }
  }
}
</script>

<style lang="scss" scoped>
  .banner {
    height: rem(170);
    overflow: hidden;

    &__img {
      width: 100%;
      height: 100%;
      vertical-align: top;
    }
  }

  .swiper-container {
    height: 100%;
  }
</style>
