import Vue from 'vue'
import Vuex from 'vuex'
import { mapState } from './mapState.js'
import { floatToFixed } from '@/utils/util.js'

Vue.use(Vuex)

const Store = new Vuex.Store({
  state: {
    customer: {}, // 用户信息
    balances: {}, // 余额信息
    gameStatus: {}, // 游戏状态
    pageTitle: null, // 头部页面标题(需要动态设置的标题)
    showSideBar: false, // 是否显示侧边栏
    token: null, // base64格式的token数据
    saveDewFigureKey: ['T032', 'B001'], // 需要保存数据的露珠图桌台号
    socketResultList: {} // 露珠图数据
  },
  getters: {
    loginName: state => state.customer.loginName,
    loginStatus: state => !!state.customer.loginName,
    balance: ({ balances }) => floatToFixed(balances.withdrawBal)
  },
  mutations: {
    logout(state, token = null) {
      this.commit('setToken', token)
      this.commit('setBalanceInfo', {})
      this.commit('setCustomerInfo', {})
    },
    setToken(state, token) {
      if (!state.token || token === null) {
        state.token = token
      }
    },
    setPageTitle(state, title) {
      state.pageTitle = title
    },
    setGameStatus(state, status) {
      state.gameStatus = status
    },
    setBalanceInfo(state, balances) {
      state.balances = balances
    },
    setCustomerInfo(state, info) {
      state.customer = info
    },
    toggleSideBar(state, status) {
      state.showSideBar = status
    },
    'socket_result list'(state, data) {
      data = JSON.parse(data) || {}
      for (const key in data) {
        if (Object.hasOwnProperty.call(data, key) && ~state.saveDewFigureKey.indexOf(key)) {
          state.socketResultList = {
            ...state.socketResultList,
            [key]: data[key]
          }
        }
      }
    },
    'socket_new shoes'(state, data) {
      data = JSON.parse(data) || {}
      if (~state.saveDewFigureKey.indexOf(data.vid)) {
        state.socketResultList[data.vid] = data
      }
    },
    'socket_close round'(state, data) {
      data = JSON.parse(data) || {}
      if (~state.saveDewFigureKey.indexOf(data.vid)) {
        if (state.socketResultList[data.vid].lastGameCode !== data.gmcode) {
          state.socketResultList[data.vid].lastGameCode = data.gmcode
          state.socketResultList[data.vid].roundRes[data.gmcode] = data
        }
      }
    }
  },
  actions: {
    getToken({ state, commit }) {
      const params = { _isNeedLoading: false }
      if (state.token) {
        return state.token
      } else {
        return this._vm.$api.getWebToken(params).then(token => {
          commit('setToken', token)
          return state.token
        })
      }
    },
    getGameStatus({ commit }) {
      const params = { _isNeedLoading: false }
      return this._vm.$api.getGameStatus(params).then(({ head, body }) => {
        if (head.errCode === '0000') {
          body.A02026.flag = 0
          commit('setGameStatus', body)
        }
        return {
          head,
          body
        }
      })
    },
    getCustomerInfo({ commit }, params) {
      const defaultParams = {
        inclEmail: 1,
        inclCredit: 1,
        inclAddress: 1,
        inclCurrency: 1,
        inclRealName: 1,
        inclMobileNo: 1,
        inclEmailBind: 1,
        inclBtcAccount: 1,
        inclVerifyCode: 1,
        inclBankAccount: 1,
        inclFirstXmFlag: 1,
        inclNickNameFlag: 1,
        inclMobileNoBind: 1,
        inclPromoAmountByMonth: 1,
        inclRebatedAmountByMonth: 1,
        inclUpgradeRequiredBetAmount: 1
      }
      params = {
        ...defaultParams,
        ...params
      }
      return this._vm.$api.getCustomerInfo(params).then(({ head, body }) => {
        if (head.errCode === '0000') {
          commit('setCustomerInfo', body)
        }
        return {
          head,
          body
        }
      })
    },
    getBalanceInfo({ commit }, params) {
      return this._vm.$api.getBalance(params).then(({ head, body }) => {
        if (head.errCode === '0000') {
          commit('setBalanceInfo', body)
        }
        return {
          head,
          body
        }
      })
    },
    login({ commit, dispatch }, params) {
      return this._vm.$api.loginEx(params).then(({ head, body }) => {
        if (head.errCode === '0000') {
          const params = {
            _isNeedLoading: false
          }
          commit('setCustomerInfo', body)
          dispatch('getCustomerInfo', params)
          dispatch('getBalanceInfo', params)
        }
        return {
          head,
          body
        }
      })
    }
  },
  modules: {},
  plugins: [mapState]
})

export default Store
