<template>
  <div v-if="showServiceDialog" class="service">
    <i class="service__icon-arrow"></i>
    <ul class="service__list">
      <li class="service__item">
        <i class="service__icon service__icon--online"></i>
        <span>在线客服</span>
      </li>
      <li class="service__item">
        <i class="service__icon service__icon--tel"></i>
        <span>免费电话</span>
      </li>
      <li @click="signOut" class="service__item">
        <i class="service__icon service__icon--setting"></i>
        <span>退出登录</span>
      </li>
      <li class="service__item service__item--tel">
        <a @click="closeDialog" href="tel:4001201721">4001201721</a>
      </li>
    </ul>
    <div @click="closeDialog" class="service__mask"></div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
  name: 'ServiceDialog',
  props: {
    showServiceDialog: Boolean
  },
  methods: {
    ...mapMutations([
      'logout'
    ]),
    signOut() {
      this.closeDialog()
      this.$api.logout().then(({ head, body }) => {
        if (head.errCode === '0000') {
          this.$router.push('/')
          this.logout(body.info)
        }
      })
    },
    closeDialog() {
      this.$emit('update:showServiceDialog', false)
    }
  }
}
</script>

