import Vue from 'vue'
import loadingComponent from './loading.vue'
import { insertAfter } from '@/utils/util.js'

const LoadingConstructor = Vue.extend(loadingComponent)

const instance = new LoadingConstructor().$mount()

function open(flag) {
  this()
  instance.show = true
  instance.flag = flag
}

function hide(flag) {
  if (instance.flag === flag) instance.show = false
}

export function install() {
  if (!instance.created) {
    instance.created = true
    insertAfter(instance.$el, document.getElementById('app'))
  }
  return install
}

install.open = open
install.hide = hide

export function loadingInstall(Vue) {
  Object.defineProperties(Vue.prototype, {
    $loading: {
      get() {
        return install
      },
      enumerable: false, // 不可枚举
      configurable: false // 不可重写
    }
  })
}

export default install
