<template>
  <!--  <transition-group :name="transitionName" tag="main" id="app" class="page">-->
  <!--    <router-view name="header" key="header"></router-view>-->
  <!--    <router-view name="default" key="default"></router-view>-->
  <!--  </transition-group>-->
  <main id="app" class="page">
    <transition :name="transitionName" @afterLeave="transitionName=null">
      <router-view name="header"></router-view>
    </transition>
    <transition :name="transitionName" @afterLeave="transitionName=null">
      <keep-alive v-if="keepAlive" :max="10">
        <router-view name="default"></router-view>
      </keep-alive>
      <router-view v-else name="default"></router-view>
    </transition>
    <!--<template v-if="keepAlive">
      <transition :name="transitionName" @afterLeave="transitionName=null">
        <keep-alive :max="10">
          <router-view name="default"></router-view>
        </keep-alive>
      </transition>
    </template>
    <template v-else>
      <transition :name="transitionName" @afterLeave="transitionName=null">
        <router-view name="default"></router-view>
      </transition>
    </template>-->
    <SideBar/>
  </main>
</template>

<script>
import SideBar from './components/SideBar'
import { mapActions } from 'vuex'

export default {
  components: {
    SideBar
  },
  data() {
    return {
      transitionName: null
    }
  },
  computed: {
    keepAlive() {
      return this.$route.meta.keepAlive !== false
    }
  },
  watch: {
    '$route'(to, from) {
      if (to.meta.index > from.meta.index) {
        this.transitionName = 'slide-left'
      } else if (to.meta.index < from.meta.index) {
        this.transitionName = 'slide-right'
      }
    }
  },
  created() {
    this.getGameStatus()
  },
  mounted() {
    const style = document.getElementById('loadingStyle')
    const div = document.getElementById('loadingDiv')
    style && style.parentNode.removeChild(style)
    div && div.parentNode.removeChild(div)
  },
  activated() {
  },
  methods: {
    ...mapActions(['getGameStatus'])
  }
}
</script>

<style lang="scss">
  @import './assets/scss/base.scss';
  @import './assets/scss/common.scss';

  #app {
    max-width: $--max-width;
    margin: 0 auto;
    overflow: hidden;
    background-color: $--background-color-base;
  }

  .slide-right-enter-active,
  .slide-right-leave-active,
  .slide-left-enter-active,
  .slide-left-leave-active {
    position: absolute;
    width: 100%;
    transition: transform 0.15s cubic-bezier(0.8, 0, 0.2, 1) 0s;
    will-change: transform;
  }

  .slide-right-leave-active {
    transform: translateX(100%);
  }

  .slide-right-enter {
    transform: translateX(-100%);
  }

  .slide-left-leave-active {
    transform: translateX(-100%);
  }

  .slide-left-enter {
    transform: translateX(100%);
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: all 0.3s ease;
  }

  .fade-enter,
  .fade-leave-to {
    opacity: 0;
  }
</style>
