'use strict'

import wms from './modules/wms.js'
import sign from '@/api/modules/sign.js'
import home from '@/api/modules/home.js'
import common from '@/api/modules/common.js'

const api = {
  ...wms,
  ...sign,
  ...home,
  ...common
}

export function apiInstall(Vue) {
  Object.defineProperties(Vue.prototype, {
    $api: {
      get() {
        return api
      },
      enumerable: false, // 不可枚举
      configurable: false // 不可重写
    }
  })
}

export default api
