import 'normalize.css/normalize.css'
import 'swiper/dist/css/swiper.css'

import Vue from 'vue'
import App from './App.vue'
import store from './store/index.js'
import router from './router/index.js'
import vueLazyLoad from 'vue-lazyload'
import VueSocketIO from 'vue-socket.io'
import config from '@/config/config.js'
import { apiInstall } from './api/api.js'
import { httpInstall } from './api/http.js'
import { utilInstall, floatToFixed } from '@/utils/util.js'
import { toastInstall } from '@/components/toast/toast.js'
import { loadingInstall } from '@/components/loading/loading.js'
import VueAwesomeSwiper from 'vue-awesome-swiper'

Vue.config.productionTip = false

Vue.use(apiInstall)
Vue.use(httpInstall)
Vue.use(utilInstall)
Vue.use(toastInstall)
Vue.use(loadingInstall)
Vue.use(VueAwesomeSwiper)
Vue.use(vueLazyLoad, {
  preLoad: 1.3,
  attempt: 1
})
Vue.use(new VueSocketIO({
  debug: false,
  connection: config.socketUrl,
  vuex: {
    store,
    actionPrefix: 'socket_',
    mutationPrefix: 'socket_'
  }
}))
Vue.filter('floatToFixed', (value, toFixed) => {
  return floatToFixed(value, toFixed)
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
