import { isObject } from '@/utils/util.js'

export function mapState(store) {
  const obj = {}
  const mapStateKey = [
    'customer',
    'balances',
    'pageTitle',
    'gameStatus',
    'token'
  ]
  const noMapStateKey = [
    'socket_result list',
    'socket_new shoes',
    'socket_close round'
  ]
  const timestamp = Date.now()
  const storage = window.localStorage
  let localState = storage.getItem('vuex_state')
  localState = (localState && JSON.parse(localState))
  localState = isObject(localState) ? localState : {}
  mapStateKey.forEach(key => {
    if (localState[key] !== undefined) obj[key] = localState[key]
  })
  if (timestamp < localState._timestamp) {
    store.replaceState({ ...store.state, ...obj })
  } else {
    storage.removeItem('vuex_state')
  }
  // 当 store 初始化后调用
  store.subscribe((mutation, state) => {
    if (~noMapStateKey.indexOf(mutation.type)) return
    storage.setItem('vuex_state', JSON.stringify({
      ...state,
      _timestamp: Date.now() + 30 * 60 * 1000
    }))
  })
}
