      height: rem(70);
      margin-top: rem(-35);
      vertical-align: top;
    }

    &__info {
      padding: rem(8) rem(5) 0;
      overflow: hidden;
      text-align: center;
    }

    &__tips {
      font-size: rem(12);
    }

    &__timer {
      font-size: rem(18);
      line-height: rem(28);
    }

    &__button {
      width: 100%;
      height: rem(32);
      font-size: rem(14);
      line-height: rem(32);
      color: $--color-primary;
    }

    &__loading {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      font-size: rem(12);
      text-align: center;
      background-color: $--background-color-base;
    }

    &__loading-content {
      position: absolute;
