<template>
  <div ref="promo" class="promo-mien">
    <div class="promo border-bottom">
      <h3 class="home__module-header">
        <img class="home__module-icon" :src="$useWebpImg('@/assets/img/home/promo/promo.png')" alt="">
        <span class="home__module-title">优惠活动</span>
        <a @click="$router.push('/promotions')" class="home__module-link" href="javascript:void (0);">更多优惠</a>
      </h3>
      <ul class="promo__list">
        <li v-for="(item,index) in promoList" :key="index"
            class="promo__item default-bg-small">
          <img class="promo__img" v-lazy="item.imgUrl" src="" alt="">
        </li>
      </ul>
    </div>
    <div class="mien">
      <h3 class="home__module-header">
        <img class="home__module-icon" :src="$useWebpImg('@/assets/img/home/promo/mien.png')" alt="">
        <span class="home__module-title">实力展示</span>
        <span @click="$router.push('/mien')" class="home__module-link">更多风采</span>
      </h3>
      <ul class="mien__list">
        <li v-for="(item,index) in mienList" :key="index"
            class="mien__item default-bg-small">
          <div class="mien__info">
            <p class="mien__title">{{item.time}}</p>
            <p class="mien__sub-title">{{item.describe}}</p>
          </div>
          <img class="mien__img" v-lazy="item.imgUrl" src="" alt="">
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import moment from 'moment'

export default {
  name: 'mien',
  data() {
    return {
      promoList: [{}, {}, {}],
      mienList: [{
        path: null,
        imgUrl: this.$useWebpImg('@/assets/img/home/promo/mien_list.png')
      }, {}, {}],
      base: window.dynamicConfig.wmsServer + '/'
    }
  },
  mounted() {
    const home = document.getElementById('home_container')
    home.addEventListener('scroll', this.scrollEvent)
  },
  methods: {
    // 读取wms优惠
    getPromoList() {
      const url = '/_promo/home_promo_list.txt'
      this.$api.getWmsStatic(url).then(({ data }) => {
        if (Array.isArray(data)) {
          this.promoList = data.map(item => {
            return {
              path: item.link,
              title: item.title,
              imgUrl: `${this.base}${item.pic}${window.isSupportWebp ? '_.webp' : ''}`
            }
          })
        }
      })
    },
    // 获取wms实力展示
    getMienList() {
      const url = '/_promo/mien_list.txt'
      this.$api.getWmsStatic(url).then(({ data }) => {
        let remark = null
        let currency = null
        if (Array.isArray(data)) {
          data = data.filter(item => {
            remark = JSON.parse(item.remark)
            currency = remark.currency.toUpperCase()
            if (+item.retain1 !== 1 || +remark.show_on_homepage !== 1) return false
            return !(this.loginStatus && currency !== 'ALL' && this.customerType !== currency)
          })
          data = data.map(item => {
            remark = JSON.parse(item.remark)
            return {
              describe: remark.home_page_title || item.title,
              path: +remark.show_on_brand === 1 ? '/mien/history' : '/mien/sponsor',
              imgUrl: `${this.base}${item.pic2}${window.isSupportWebp ? '_.webp' : ''}`,
              time: item.time_begin && !remark.home_page_time ? moment(item.time_begin * 1000).format('Y.MM') : null
            }
          })
          this.mienList = [{
            path: null,
            imgUrl: this.$useWebpImg('@/assets/img/home/promo/mien_list.png')
          }, ...data]
        }
      })
    },
    // 主页容器滚动处理方法
    scrollEvent(e) {
      const offsetTop = this.$refs.promo.offsetTop
      const scrollTop = e.target.scrollTop + e.target.clientHeight
      if (offsetTop <= scrollTop + 20) {
        this.getMienList()
        this.getPromoList()
        e.target.removeEventListener('scroll', this.scrollEvent)
      }
    }
  }
}

</script>

