<template>
  <header class="header border-bottom">
    <div class="header__left">
      <button v-if="showMenuButton" @click="()=>toggleSideBar(true)"
              class="header__button header__button--menu"></button>
      <slot v-else name="left">
        <button @click="callback" class="header__button header__button--back">
          返回
        </button>
      </slot>
    </div>
    <h1 class="header__title">
      <img v-show="showLogo" class="header__logo" :src="$useWebpImg('@/assets/img/header/logo.png')" alt="K8">
      <span v-show="!showLogo">{{title}}</span>
    </h1>
    <div class="header__right">
      <slot name="right">
        <button @click="showServiceDialog=!showServiceDialog" class="header__button header__button--service"></button>
        <ServiceDialog :showServiceDialog.sync="showServiceDialog"></ServiceDialog>
      </slot>
    </div>
  </header>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import ServiceDialog from '@/components/dialog/ServiceDialog.vue'

export default {
  name: 'Header',
  components: { ServiceDialog },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.formPath = from.fullPath
    })
  },
  data() {
    return {
      formPath: null,
      showServiceDialog: false
    }
  },
  computed: {
    ...mapState(['pageTitle']),
    title() {
      return this.pageTitle || this.$route.meta.title
    },
    backPath() {
      return this.$route.meta.backPath || null
    },
    showLogo() {
      return this.$route.meta.showLogo === true
    },
    showMenuButton() {
      return this.$route.meta.showMenuButton === true
    }
  },
  methods: {
    ...mapMutations([
      'toggleSideBar'
    ]),
    callback() {
      if (this.backPath) {
        this.$router.push(this.backPath)
      } else if (this.formPath !== '/') {
        this.$router.back()
      } else {
        this.$router.replace('/')
      }
    }
  }
}
</script>

