'use strict'
import Vue from 'vue'

const wmsServer = window.dynamicConfig.wmsServer
const wmsPath = window.dynamicConfig.wmsPath
const cdnPath = window.dynamicConfig.cdnPath

// https://fm.libo166.com/cdn/A02FM/_wms/js/_l/electronicgames/games.js
// https://fm.libo166.com/cdn/A02FM/_wms/static/_l/_data/_banner/banner.txt
// https://fm.libo166.com/cdn/A02FM/_wms/img/_l/_banner/banner-index_new.jpg_.webp
// 获取wms静态文件
export function getWmsStatic(url, param) {
  url = `${wmsServer + cdnPath}/_wms/static/${wmsPath}/_data` + url
  return Vue.prototype.$axios.get(url, param)
}

export default {
  getWmsStatic
}
