import Vue from 'vue'
import toastComponent from './toast.vue'
import { insertAfter, isObject } from '@/utils/util.js'

const LoadingConstructor = Vue.extend(toastComponent)

const instance = new LoadingConstructor().$mount()

const defaultParams = {
  type: 'success',
  duration: 2000,
  message: null,
  callback: () => null
}

function success(params) {
  if (isObject(params)) {
    params.type = 'success'
  } else {
    params = {
      type: 'success',
      message: params + ''
    }
  }
  this(params)
}

function error(params) {
  if (isObject(params)) {
    params.type = 'error'
  } else {
    params = {
      type: 'error',
      message: params + ''
    }
  }
  this(params)
}

/**
 * @description 初始化并打开toast提示，参数说明
 * @params {Object} params
 * @params type
 * */
export function install(params = {}) {
  params = {
    ...defaultParams,
    ...params
  }
  if (!instance.created) {
    instance.created = true
    insertAfter(instance.$el, document.getElementById('app'))
  }
  if (params.duration) {
    instance.timer = setTimeout(() => {
      instance.show = false
      clearTimeout(instance.timer)
    }, params.duration)
  }
  instance.show = true
  instance.type = params.type
  instance.message = params.message
  instance.callback = params.callback
}

install.success = success
install.error = error

export function toastInstall(Vue) {
  Object.defineProperties(Vue.prototype, {
    $toast: {
      get() {
        return install
      },
      enumerable: false, // 不可枚举
      configurable: false // 不可重写
    }
  })
}

export default install
